/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --ff-primary: "Nunito Sans", sans-serif;
}

:root {
  --fw-light: 300;
  --fw-regular: 600;
  --fw-bold: 800;
}

body {
  font-family: var(--ff-primary);
  font-size: 0.875rem;
  font-weight: var(--fw-regular);
  color: var(--clr-text);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

:root {
  --clr-text: hsl(200, 15%, 8%);
  --clr-element: hsl(0, 0%, 100%);
  --clr-background: hsl(0, 0%, 98%);
  --clr-input: hsl(0, 0%, 52%);
}

[data-theme=dark] {
  --clr-text: hsl(0, 0%, 100%);
  --clr-element: hsl(209, 23%, 22%);
  --clr-background: hsl(207, 26%, 17%);
  --clr-input: hsl(0, 0%, 100%);
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

li {
  list-style: none;
}

dt,
dd {
  display: inline-block;
}

:root {
  --bs: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

body {
  background-color: var(--clr-background);
}

.main {
  padding: 1.5rem 0.875rem;
}

.container {
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
}

.button {
  display: flex;
  align-items: center;
  background-color: var(--clr-element);
  color: var(--text-);
  font-size: 1rem;
  border: none;
  padding: 0.25rem 1.375rem;
  border-radius: 0.1875rem;
  box-shadow: var(--bs);
  cursor: pointer;
}
.button__icon-arrow {
  display: flex;
  align-items: center;
  margin-right: 0.75rem;
  font-size: 1rem;
}
@media (min-width: 90rem) {
  .button__icon-arrow {
    font-size: 1.125rem;
  }
}
@media (min-width: 90rem) {
  .button {
    font-size: 1.1875rem;
    padding: 0.375rem 2rem;
  }
}

.header {
  background-color: var(--clr-element);
  padding: 2.125rem 0.875rem;
  box-shadow: var(--bs);
}
.header__layout {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header h1 {
  font-size: 1rem;
  font-weight: var(--fw-bold);
}
@media (min-width: 75rem) {
  .header h1 {
    font-size: 1.75rem;
  }
}
.header__theme-switcher {
  background-color: transparent;
  padding: 0;
  border: none;
  color: var(--clr-text);
  font-weight: var(--fw-regular);
  display: flex;
  align-items: center;
  gap: 0.625rem;
  cursor: pointer;
}
.header__theme-switcher:focus {
  outline: none;
}
@media (min-width: 75rem) {
  .header__theme-switcher svg {
    font-size: 1rem;
  }
}
@media (min-width: 75rem) {
  .header__theme-switcher {
    font-size: 1.125rem;
  }
}

.countries {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 16.5rem);
  gap: 4.625rem;
}

.card {
  background-color: var(--clr-element);
  box-shadow: var(--bs);
  border-radius: 0.375rem;
  overflow: hidden;
  cursor: pointer;
}
.card img {
  width: 100%;
  height: 10rem;
  object-fit: cover;
}
.card__text-container {
  padding: 1.875rem 1.5rem;
}
.card__text-container h3 {
  font-size: 1.125rem;
  font-weight: var(--fw-bold);
  margin-bottom: 1.375rem;
}
.card__text-container dl {
  font-size: 1rem;
}
.card__text-container dd {
  font-weight: var(--fw-light);
  margin-bottom: 0.75rem;
}

.search {
  margin-bottom: 3rem;
}
@media (min-width: 75rem) {
  .search {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.search__input-container {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  max-width: 30rem;
}
.search__icon-search {
  display: flex;
  position: absolute;
  left: 2rem;
  z-index: 100;
  font-size: 0.875rem;
}
@media (min-width: 75rem) {
  .search__icon-search {
    font-size: 1rem;
  }
}
.search__item {
  background-color: var(--clr-element);
  box-shadow: var(--bs);
  color: var(--clr-text);
  font-size: 0.875rem;
  border-radius: 0.375rem;
  padding: 1.25rem 2rem 1.25rem;
}
@media (min-width: 75rem) {
  .search__item {
    font-size: 1rem;
  }
}
.search__input {
  padding-left: 4rem;
  border: none;
  width: 100%;
}
.search__input:focus {
  outline: none;
}
.search__input::placeholder {
  color: var(--clr-input);
}
.search__select-container {
  position: relative;
  width: 15rem;
}
@media (min-width: 75rem) {
  .search__select-container {
    width: 16rem;
  }
}
.search__select {
  display: flex;
  align-items: center;
  margin-top: 2.5rem;
  cursor: pointer;
  border: none;
  width: 100%;
}
.search__select:focus {
  outline: none;
}
@media (min-width: 75rem) {
  .search__select {
    margin-top: 0;
  }
}
.search__icon-chevron {
  display: flex;
  align-items: center;
  margin-left: 4rem;
  font-size: 0.625rem;
}
@media (min-width: 75rem) {
  .search__icon-chevron {
    font-size: 0.875rem;
  }
}
.search__regions {
  position: absolute;
  top: 4.1875rem;
  left: 0;
  width: 100%;
  display: none;
  cursor: pointer;
}
.search__regions li {
  display: flex;
}
@media (min-width: 75rem) {
  .search__regions {
    top: 4.375rem;
  }
}
.search__regions--open {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 0.375rem;
}
.search__region {
  color: var(--clr-text);
  background-color: transparent;
  padding: 0;
  border: none;
  cursor: pointer;
  transition: transform 31.25ms;
}
.search__region:focus {
  outline: none;
}
@media (hover: hover) {
  .search__region:hover {
    transform: scale(1.025);
  }
}

.details {
  padding: 2.5rem 1.75rem;
}
.details img {
  margin-left: auto;
  margin-right: auto;
  box-shadow: var(--bs);
  max-height: 20rem;
  aspect-ratio: 4/3;
  object-fit: cover;
}
.details__grid {
  margin-top: 4rem;
  margin-bottom: 4rem;
}
.details__text-container {
  margin-top: 3.15rem;
}
.details__text-container h2 {
  font-size: 1.625rem;
  font-weight: var(--fw-bold);
  margin-bottom: 1.875rem;
}
.details__description-list {
  display: grid;
  gap: 2.875rem;
}
.details__borders {
  display: flex;
  flex-flow: row wrap;
  gap: 1.5rem 0.625rem;
  margin-top: 1.5rem;
}
.details__borders button {
  padding: 0;
  justify-content: center;
  width: 6rem;
  height: 1.75rem;
  font-size: 0.875rem;
}
.details__borders__title, .details__borders__none {
  font-size: 1rem;
}
.details__borders__none {
  font-weight: var(--fw-light);
}
@media (max-width: 74.9375rem) {
  .details__borders__title {
    width: 100%;
  }
}

@media (min-width: 75rem) {
  .details {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
  .details__image-container {
    display: grid;
    align-items: center;
  }
  .details img {
    width: 100%;
    max-height: 100%;
  }
  .details__text-container {
    margin-top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .details__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 9.125rem;
    margin-top: 5rem;
  }
  .details__description-list {
    grid-template-columns: 1fr 1fr;
    gap: 0;
  }
  .details__borders {
    margin-top: 4rem;
  }
}
@media (min-width: 90rem) {
  .details h2 {
    font-size: 2.25rem;
  }
  .details__borders button {
    font-size: 1rem;
  }
  .details__borders__title, .details__borders__none {
    font-size: 1.125rem;
  }
}
.details dl {
  font-size: 1rem;
}
@media (min-width: 90rem) {
  .details dl {
    font-size: 1.125rem;
  }
}
.details dd {
  font-weight: var(--fw-light);
  margin-bottom: 0.5rem;
}